<template>
    <div>
        <div v-if="isInstallMetaMask">
            메타마스크 설치됨
            <div v-if="myAddress==null">
                <button v-on:click="connectToMetaMask">
                    메타마스크 연결
                </button>
            </div>
            <!--메타마스크가 연결됬을때-->
            <div v-else>
                <div v-if="myContract == null">
                    <button v-on:click="connectToContract">
                        컨트랙트 연결
                    </button>
                </div>
                <div v-else>
                    <!--컨트랙트가 연결됌-->
                    <div>
                        <!--ownerOf 구현-->
                        <input type="text" placeholder="tokenId" v-model="ownerOf_tokenId">
                        <button v-on:click="requestOwnerOf">
                            ownerOf 요청
                        </button>
                    </div>
                    <br>
                    <div>
                        <!--balanceOf 구현-->
                        <input type="text" placeholder="address" v-model="balanceOf_address">
                        <button v-on:click="requestBalanceOf">
                            balanceOf 요청
                        </button>
                    </div>
                    <br>
                    <div>
                        <!--mint 구현-->
                        <input type="text" placeholder="mint to address" v-model="mint_toAddress"><br>
                        <input type="text" placeholder="tokenId" v-model="mint_tokenId"><br>
                        <input type="text" placeholder="url" v-model="mint_url"><br>
                        <button v-on:click="requestMint">
                            mint 요청
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    import { ethers } from "ethers";
    import abi from "../assets/abi.json";     //abi를 인터넷에서 연결시킬수 있을까? 보안문제는?

    export default {
        name: 'HelloWorld',
        props: {
            msg: String
        },

        data() {
            return {
                isInstallMetaMask: window.ethereum != null && window.ethereum.isMetaMask == true,
                myAddress: null,
                myContract: null,

                ownerOf_tokenId : null,
                ownerOf_result : null,

                balanceOf_address : null,
                balanceOf_result: null,

                mint_toAddress: null,
                mint_tokenId: null,
                mint_url: null,
                mint_result : null
            }
        },

        methods: {
            async connectToMetaMask() {
                console.log("Click connectToMetaMask");
                var addressArray = await window.ethereum.request({ method: "eth_requestAccounts" });
                //주소값이 배열로 들어있기 떄문에 배열로 선언할수밖에?

                if (addressArray.length > 0) {
                    this.myAddress = addressArray[0];
                }
                console.log(addressArray);
            },

            async connectToContract() {
                console.log("Click connectToContract");

                var contractAddress = "0x0D303C4e1912AE99d64aEa8A5B06f8b6d791e207";
                //이전컨트랙트 주소 0x4132ABB015396B63749565F9dd38d2F24E54E1a7

                //컨트랙트접속을 위해 주소랑 abi가 필요
                //signer -> 자동서명을 위한 것
                //Web3Provider(), Contract() -> Class라 new로 할당
                var provider = new ethers.providers.Web3Provider(window.ethereum);
                var signer = provider.getSigner();
                this.myContract = new ethers.Contract(contractAddress, abi, signer);

                console.log(this.myContract);
            },

            async requestOwnerOf() {
                console.log("Click requestOwnerOf");
                console.log(this.ownerOf_tokenId);

                this.ownerOf_result = await this.myContract.ownerOf(this.ownerOf_tokenId);
                console.log(this.ownerOf_result);
                alert("오너 주소 : " + this.ownerOf_result);
            },

            async requestBalanceOf() {
                console.log("Click requestBalanceOf");
                console.log(this.balanceOf_address);

                this.balanceOf_result = await this.myContract.balanceOf(this.balanceOf_address);
                console.log(this.balanceOf_result);
                alert("가지고 있는 토큰 개수 : " + this.balanceOf_result);
            },

            async requestMint() {
                console.log("Click requestMint");
                console.log(this.mint_toAddress);
                console.log(this.mint_tokenId);
                console.log(this.mint_url);

                this.mint_result = await this.myContract.mint(this.mint_toAddress, this.mint_tokenId, this.mint_url);
                console.log(this.mint_result);
                alert("주소 : " + this.mint_toAddress + "\n토큰Id : " + this.mint_tokenId + "\nurl : " + this.mint_url);
            }
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h3 {
        margin: 40px 0 0;
    }

    ul {
        list-style-type: none;
        padding: 0;
    }

    li {
        display: inline-block;
        margin: 0 10px;
    }

    a {
        color: #42b983;
    }
</style>
